const tabsBtn = document.querySelectorAll('.tabs__btn');
const tabsContent = document.querySelectorAll(".tabs__content");

tabsContent.forEach(el => {
    const switchContent = (path) => {
        tabsContent.forEach(el => {el.classList.remove('tabs__content--active')});
        document.querySelector(`[data-tabs-target="${path}"]`).classList.add('tabs__content--active');
    };

    tabsBtn.forEach(el => {
        el.addEventListener('click', (e) => {
            const tabsPath = el.getAttribute("data-tabs-path");
            tabsBtn.forEach(el => {el.classList.remove('stages__tabs-btn--active')});
            document.querySelector(`[data-tabs-path="${tabsPath}"]`).classList.add('stages__tabs-btn--active');
            switchContent(tabsPath);
        })
    });
});